import dayjs from 'dayjs';

/***
 *   -    
 */
export const DATE_FORMAT_Y = 'YYYY';

/**
 *   - ,   ,   
 */
export const DATE_FORMAT_D_M_Y = 'D MMMM YYYY';

/**
 * @description     
 * {@link https://developer.mozilla.org/ru/docs/Web/JavaScript/Reference/Global_Objects/Math/random}
 * @param {Number} min
 * @param {Number} max
 * @returns {Number}       .     min (   ,   min,  min  )    (  ) max.
 */
export const getRandomIntInclusive = (min, max) => {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

/**
 * @description   
 * {@link https://habr.com/ru/articles/312880/}
 * @returns {boolean}
 */
export const getBooleanValue = () => (Math.floor(Math.random() * 2) === 0);

/**
 * @description     
 * @param {Array} items - 
 * @returns  
 */
export const getRandomArrayElement = (items) => items[Math.floor(Math.random() * items.length)];

/**
 * @description '  '
 * @param {string} dueDate -    iso-date
 * @param {string} dateFormat -    
 * @returns {string}  
 */
export const humanizeTaskDueDate = (dueDate, dateFormat) => dueDate ? dayjs(dueDate).format(dateFormat) : '';

/**
 * @description   0   ,    10
 * @param {Number} min
 * @param {Number} max
 * @returns {string}
 */
export const createDateMonth = (min, max) => {
  const month = getRandomIntInclusive(min, max);
  return month < 10 ? `0${month}` : `${month}`;
};

/**
 * @description   
 * @returns {string}
 */
export const generateRandomDate = () => {
  const randomYear = getRandomIntInclusive(1900, 2023);
  const randomMonth = createDateMonth(1, 12);
  const randomDay = createDateMonth(1, 31);

  return `${randomYear}-${randomMonth}-${randomDay}`;
};

/**
 * @description    
 * @param {Array} arr - 
 * @returns {Array}
 */
export const getRandomArray = (arr) => {
  const rand = getRandomIntInclusive(1, arr.length);
  const newArr = [];

  while (newArr.length !== rand) {
    const elem = arr[getRandomIntInclusive(0, arr.length - 1)];
    if (newArr.indexOf(elem) === -1) {
      newArr.push(elem);
    }
  }

  return newArr;
};

/**
 * @description        
 * {@link https://www.cyberforum.ru/javascript/thread938011.html}
 * @param {Number} time
 * @returns {string}
 */
export const getTimeInMinute = (time) => {
  //   60,    
  if (time < 60) {
    return `${time}m`;
  }

  //     0,    
  if (time % 60 === 0) {
    const hours = time / 60;
    return `${hours}h`;
  }

  //    ,     
  if (time % 60 !== 0) {
    const minutes = time % 60;
    const hours = (time - minutes) / 60;

    return `${hours}h ${minutes}m`;
  }
};

/**
 * @description      
 * @param {array} array -   
 * @returns {array} -    
 */
export const getRandomArrayPart = (array) => {
  const max = getRandomIntInclusive(0, array.length - 1);
  const min = getRandomIntInclusive(0, max);
  return array.slice(min, max + 1);
};

/**
 * @description     ( true -  , false -   )
 * @param {boolean} element -  
 * @param {string} className - ,   
 * @returns
 */
export const changeClassName = (element, className) => element ? className : '';

/**
 * @description        ,
      sort
 * @param {string} dateA
 * @param {string} dateB
 * @returns
 */
export function getWeightForNullDate(dateA, dateB) {
  //   . ..   
  if (dateA === null && dateB === null) {
    return 0;
  }

  if (dateA === null) {
    return 1;
  }

  if (dateB === null) {
    return -1;
  }

  return null;
}

/**
 * @description    
 * @param {Object} filmDateA
 * @param {Object} filmDateB
 * @returns
 */
export function sortFilmDateUp(filmDateA, filmDateB) {
  const weight = getWeightForNullDate(filmDateA.filmInfo.release.date, filmDateB.filmInfo.release.date);

  return weight ?? dayjs(filmDateA.filmInfo.release.date).diff(dayjs(filmDateB.filmInfo.release.date));
}

/**
 * @description    
 * @param {Object} filmDateA
 * @param {Object} filmDateB
 * @returns
 */
export function sortFilmDateDown(filmDateA, filmDateB) {
  const weight = getWeightForNullDate(filmDateA.filmInfo.release.date, filmDateB.filmInfo.release.date);

  return weight ?? dayjs(filmDateB.filmInfo.release.date).diff(dayjs(filmDateA.filmInfo.release.date));
}

export function updateItem(items, update) {
  return items.map((item) => item.id === update.id ? update : item);
}

